// Core Web Vitals optimization utilities
import { getCLS, getFID, getFCP, getLCP, getTTFB } from 'web-vitals';

// Enhanced Web Vitals tracking with improved thresholds
export const trackWebVitals = (onPerfEntry) => {
  if (onPerfEntry && onPerfEntry instanceof Function) {
    // Enhanced tracking with better error handling
    const trackMetric = (metric) => {
      try {
        // Add performance grade based on Google's thresholds
        const grade = getPerformanceGrade(metric);
        const enhancedMetric = {
          ...metric,
          grade,
          timestamp: Date.now(),
          url: window.location.href,
          userAgent: navigator.userAgent,
          connection: getConnectionInfo()
        };
        
        onPerfEntry(enhancedMetric);
        
        // Send to analytics if available
        if (window.gtag) {
          window.gtag('event', 'web_vitals', {
            event_category: 'Performance',
            event_label: metric.name,
            value: Math.round(metric.value),
            custom_parameter_1: grade
          });
        }
      } catch (error) {
        console.warn('Web Vitals tracking error:', error);
      }
    };

    getCLS(trackMetric);
    getFID(trackMetric);
    getFCP(trackMetric);
    getLCP(trackMetric);
    getTTFB(trackMetric);
  }
};

// Performance grade calculation based on Google's Core Web Vitals thresholds
const getPerformanceGrade = (metric) => {
  const thresholds = {
    CLS: { good: 0.1, needsImprovement: 0.25 },
    FID: { good: 100, needsImprovement: 300 },
    FCP: { good: 1800, needsImprovement: 3000 },
    LCP: { good: 2500, needsImprovement: 4000 },
    TTFB: { good: 800, needsImprovement: 1800 }
  };

  const threshold = thresholds[metric.name];
  if (!threshold) return 'unknown';

  if (metric.value <= threshold.good) return 'good';
  if (metric.value <= threshold.needsImprovement) return 'needs-improvement';
  return 'poor';
};

// Get connection information for context
const getConnectionInfo = () => {
  if ('connection' in navigator) {
    const conn = navigator.connection;
    return {
      effectiveType: conn.effectiveType,
      downlink: conn.downlink,
      rtt: conn.rtt,
      saveData: conn.saveData
    };
  }
  return null;
};

// Performance optimization helpers
export const performanceOptimizations = {
  // Preload critical resources
  preloadCriticalResources: () => {
    // Preload critical fonts
    const fontPreloads = [
      'https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap'
    ];

    fontPreloads.forEach(href => {
      const link = document.createElement('link');
      link.rel = 'preload';
      link.as = 'style';
      link.href = href;
      link.crossOrigin = 'anonymous';
      document.head.appendChild(link);
    });
  },

  // Optimize images with lazy loading and responsive sizing
  optimizeImages: () => {
    const images = document.querySelectorAll('img:not([loading])');
    images.forEach(img => {
      // Add lazy loading
      img.loading = 'lazy';
      
      // Add decode async for better performance
      img.decoding = 'async';
      
      // Optimize based on viewport
      if ('IntersectionObserver' in window) {
        const imageObserver = new IntersectionObserver((entries) => {
          entries.forEach(entry => {
            if (entry.isIntersecting) {
              const image = entry.target;
              // Switch to high-res version when in viewport
              if (image.dataset.highRes) {
                image.src = image.dataset.highRes;
              }
              imageObserver.unobserve(image);
            }
          });
        });
        
        imageObserver.observe(img);
      }
    });
  },

  // Minimize layout shifts
  minimizeLayoutShift: () => {
    // Set explicit dimensions for dynamic content
    const dynamicElements = document.querySelectorAll('[data-dynamic]');
    dynamicElements.forEach(element => {
      const rect = element.getBoundingClientRect();
      if (rect.height > 0 && !element.style.minHeight) {
        element.style.minHeight = `${rect.height}px`;
      }
    });

    // Optimize font loading to prevent FOIT/FOUT
    if ('fonts' in document) {
      document.fonts.ready.then(() => {
        document.body.classList.add('fonts-loaded');
      });
    }
  },

  // Reduce main thread blocking
  optimizeMainThread: () => {
    // Use requestIdleCallback for non-critical work
    const scheduleWork = (work) => {
      if ('requestIdleCallback' in window) {
        requestIdleCallback(work);
      } else {
        setTimeout(work, 1);
      }
    };

    // Defer non-critical scripts
    const nonCriticalScripts = document.querySelectorAll('script[data-defer]');
    nonCriticalScripts.forEach(script => {
      scheduleWork(() => {
        const newScript = document.createElement('script');
        newScript.src = script.src;
        newScript.async = true;
        document.body.appendChild(newScript);
      });
    });
  },

  // Optimize third-party scripts
  optimizeThirdParty: () => {
    // Delay loading of third-party widgets until user interaction
    const delayedElements = document.querySelectorAll('[data-delay-load]');
    
    const loadDelayedElements = () => {
      delayedElements.forEach(element => {
        const src = element.dataset.src;
        if (src) {
          if (element.tagName === 'SCRIPT') {
            const script = document.createElement('script');
            script.src = src;
            script.async = true;
            element.parentNode.replaceChild(script, element);
          } else if (element.tagName === 'IFRAME') {
            element.src = src;
          }
        }
      });
    };

    // Load on first user interaction
    const events = ['mousedown', 'mousemove', 'keypress', 'scroll', 'touchstart'];
    const triggerLoad = () => {
      loadDelayedElements();
      events.forEach(event => {
        window.removeEventListener(event, triggerLoad, { passive: true });
      });
    };

    events.forEach(event => {
      window.addEventListener(event, triggerLoad, { passive: true });
    });

    // Fallback: load after 5 seconds
    setTimeout(loadDelayedElements, 5000);
  }
};

// Initialize performance optimizations
export const initializePerformanceOptimizations = () => {
  // Run optimizations when DOM is ready
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', () => {
      runOptimizations();
    });
  } else {
    runOptimizations();
  }
};

const runOptimizations = () => {
  // Run optimizations with error handling
  Object.values(performanceOptimizations).forEach(optimization => {
    try {
      optimization();
    } catch (error) {
      console.warn('Performance optimization error:', error);
    }
  });
};

// Resource hints for better loading performance
export const addResourceHints = () => {
  const hints = [
    { rel: 'dns-prefetch', href: 'https://fonts.googleapis.com' },
    { rel: 'dns-prefetch', href: 'https://fonts.gstatic.com' },
    { rel: 'dns-prefetch', href: 'https://api.emailjs.com' },
    { rel: 'preconnect', href: 'https://fonts.googleapis.com' },
    { rel: 'preconnect', href: 'https://fonts.gstatic.com', crossorigin: true }
  ];

  hints.forEach(hint => {
    const link = document.createElement('link');
    Object.keys(hint).forEach(key => {
      if (key === 'crossorigin' && hint[key] === true) {
        link.setAttribute('crossorigin', '');
      } else {
        link.setAttribute(key, hint[key]);
      }
    });
    document.head.appendChild(link);
  });
};

const webVitalsUtils = {
  trackWebVitals,
  performanceOptimizations,
  initializePerformanceOptimizations,
  addResourceHints
};

export default webVitalsUtils;