import React, { useState } from 'react';
import emailjs from '@emailjs/browser';
import * as FORM_CONSTANTS from '../constants/formConstants';
import ServiceSelectionPage from './pages/ServiceSelectionPage';
import ContactAndBookingPage from './pages/ContactAndBookingPage';
import SimpleProgressBar from './SimpleProgressBar';
import useFormPersistence from '../hooks/useFormPersistence';
import { sanitizeFormData } from '../utils/sanitization';
import formAnalytics from '../utils/analytics';
import SocialProof from './common/SocialProof';
import FAQ from './common/FAQ';

// Simplified initial form data for 2-page flow
const initialFormData = {
  // Service Selection (Page 1)
  selectedWindowService: null,
  propertyType: '',
  bedrooms: '',
  selectedFrequency: '',
  initialWindowPrice: 0,
  hasConservatory: false,
  hasExtension: false,
  conservatorySurcharge: 0,
  extensionSurcharge: 0,
  additionalServices: {
    [FORM_CONSTANTS.ADDON_GUTTER_CLEARING]: false,
    [FORM_CONSTANTS.ADDON_FASCIA_SOFFIT_GUTTER]: false
  },
  gutterClearingServicePrice: 0,
  fasciaSoffitGutterServicePrice: 0,
  grandTotal: 0,
  subTotalBeforeDiscount: 0,

  // Booking type flags
  isCustomQuote: false,
  isCommercial: false,
  isResidential: false,
  isGeneralEnquiry: false,

  // General enquiry details
  generalEnquiryDetails: {
    requestedServices: {},
    requestedFrequency: '',
    otherServiceText: '',
    enquiryComments: ''
  },

  // Contact details (Page 2)
  customerName: '',
  email: '',
  mobile: '',
  addressLine1: '',
  addressLine2: '',
  townCity: '',
  postcode: '',
  selectedDate: null,
  specialInstructions: '',
  serviceArea: '',
  visitDay: '',

  // Submission data
  recaptchaToken: '',
  bookingReference: '',
  isSubmitted: false
};

// Helper function to format price
const formatPrice = (price) => {
  const num = parseFloat(price) || 0;
  return num.toFixed(2);
};

// Helper function to format date
const formatDate = (dateString) => {
  if (!dateString) return 'N/A';
  try {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-GB', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  } catch {
    return dateString;
  }
};

// Map form data to email template parameters
const mapFormDataToEmailTemplate = (formData) => {
  const isStandardResidential = formData.isResidential && !formData.isCustomQuote && !formData.isCommercial && !formData.isGeneralEnquiry;
  
  return {
    // Contact Information
    customerName: formData.customerName || 'N/A',
    email: formData.email || 'N/A',
    mobile: formData.mobile || 'N/A',
    addressLine1: formData.addressLine1 || '',
    addressLine2: formData.addressLine2 || '',
    townCity: formData.townCity || '',
    postcode: formData.postcode || '',
    specialInstructions: formData.specialInstructions || 'None',

    // Service Area Information
    serviceArea: formData.serviceArea || 'N/A',
    visitDay: formData.visitDay || 'N/A',
    selectedDate: formatDate(formData.selectedDate),

    // Booking type flags
    isStandardResidential: isStandardResidential ? 'true' : '',
    isCustomQuote: formData.isCustomQuote ? 'true' : '',
    isCommercial: formData.isCommercial ? 'true' : '',
    isGeneralEnquiry: formData.isGeneralEnquiry ? 'true' : '',

    // Standard Residential Details
    propertyType: isStandardResidential ? (formData.propertyType || 'N/A') : 'N/A',
    bedrooms: isStandardResidential ? (formData.bedrooms || 'N/A') : 'N/A',
    selectedFrequency: isStandardResidential ? (formData.selectedFrequency || 'N/A') : 'N/A',
    initialWindowPrice: isStandardResidential ? formatPrice(formData.initialWindowPrice) : '0.00',

    // Property Features
    hasConservatory: isStandardResidential && formData.hasConservatory ? 'true' : '',
    conservatorySurcharge: isStandardResidential ? formatPrice(formData.conservatorySurcharge) : '0.00',
    hasExtension: isStandardResidential && formData.hasExtension ? 'true' : '',
    extensionSurcharge: isStandardResidential ? formatPrice(formData.extensionSurcharge) : '0.00',

    // Additional Services
    hasGutterClearing: isStandardResidential && formData.additionalServices?.[FORM_CONSTANTS.ADDON_GUTTER_CLEARING] ? 'true' : '',
    gutterClearingPrice: isStandardResidential ? formatPrice(formData.gutterClearingServicePrice) : '0.00',
    hasFasciaSoffit: isStandardResidential && formData.additionalServices?.[FORM_CONSTANTS.ADDON_FASCIA_SOFFIT_GUTTER] ? 'true' : '',
    fasciaSoffitPrice: isStandardResidential ? formatPrice(formData.fasciaSoffitGutterServicePrice) : '0.00',

    // Pricing
    grandTotal: formatPrice(formData.grandTotal),
    subTotalBeforeDiscount: isStandardResidential ? formatPrice(formData.subTotalBeforeDiscount) : '0.00',

    // General Enquiry Details
    generalEnquiryServices: formData.isGeneralEnquiry ? JSON.stringify(formData.generalEnquiryDetails?.requestedServices || {}) : '',
    generalEnquiryFrequency: formData.isGeneralEnquiry ? (formData.generalEnquiryDetails?.requestedFrequency || 'N/A') : 'N/A',
    generalEnquiryOtherText: formData.isGeneralEnquiry ? (formData.generalEnquiryDetails?.otherServiceText || '') : '',
    generalEnquiryComments: formData.isGeneralEnquiry ? (formData.generalEnquiryDetails?.enquiryComments || '') : '',

    // Meta information
    submissionType: formData.isCommercial ? 'Commercial Enquiry' : 
                   formData.isCustomQuote ? 'Custom Quote Request' :
                   formData.isGeneralEnquiry ? 'General Enquiry' : 'Standard Booking',
    
    'g-recaptcha-response': formData.recaptchaToken || ''
  };
};

function BookingForm() {
  const [currentPage, setCurrentPage] = useState(1); // 1 = Service Selection, 2 = Contact & Booking
  const [formData, setFormData] = useState(initialFormData);
  const [isLoading, setIsLoading] = useState(false);
  const [submissionError, setSubmissionError] = useState(null);
  
  // Initialize analytics tracking
  React.useEffect(() => {
    formAnalytics.trackFormStart('booking');
    formAnalytics.trackStepStart(1, 'service_selection');
    formAnalytics.setupPageVisibilityTracking();
    
    return () => {
      // Cleanup: track abandonment if form wasn't completed
      const isCompleted = formData.isSubmitted;
      if (!isCompleted) {
        formAnalytics.trackFormAbandonment(currentPage, getStepName(currentPage), 'component_unmount');
      }
    };
  }, [currentPage, formData.isSubmitted]);

  const getStepName = (step) => {
    switch(step) {
      case 1: return 'service_selection';
      case 2: return 'additional_services';
      case 3: return 'contact_details';
      case 4: return 'confirmation';
      default: return 'unknown';
    }
  };
  
  // Add form persistence to prevent data loss
  const { clearSavedData } = useFormPersistence(formData, setFormData);

  const handleServiceSelectionNext = () => {
    setCurrentPage(2);
    window.scrollTo(0, 0);
  };

  const handleContactBack = () => {
    setCurrentPage(1);
    window.scrollTo(0, 0);
  };

  const handleSubmit = async (finalFormData) => {
    if (!finalFormData.recaptchaValue) {
      setSubmissionError("Please complete the reCAPTCHA verification.");
      return;
    }

    setIsLoading(true);
    setSubmissionError(null);

    try {
      // Prepare final form data
      const submissionData = {
        ...formData,
        ...finalFormData,
        recaptchaToken: finalFormData.recaptchaValue
      };

      // Sanitize form data before submission
      const sanitizedFormData = sanitizeFormData(submissionData);
      
      // Send email using EmailJS
      const serviceId = process.env.REACT_APP_EMAILJS_SERVICE_ID;
      const templateId = process.env.REACT_APP_EMAILJS_TEMPLATE_ID;
      const userId = process.env.REACT_APP_EMAILJS_PUBLIC_KEY;

      if (serviceId && templateId && userId) {
        const templateParams = mapFormDataToEmailTemplate(sanitizedFormData);
        
        // Generate booking reference
        const bookingRef = `SWC${Date.now().toString(36).toUpperCase().slice(-6)}`;
        templateParams.bookingReference = bookingRef;
        templateParams.timestamp = new Date().toLocaleString('en-GB', { 
          dateStyle: 'full', 
          timeStyle: 'short' 
        });
        
        await emailjs.send(serviceId, templateId, templateParams, userId);
        
        // Update form data and show success page
        setFormData(prev => ({ 
          ...prev, 
          ...submissionData,
          bookingReference: bookingRef, 
          isSubmitted: true 
        }));
        setCurrentPage(3); // Success page
        clearSavedData();
        
      } else {
        throw new Error('Email service not configured. Please check environment variables.');
      }
      
    } catch (error) {
      const errorMessage = error.message || 'Unknown error occurred';
      const submissionType = formData.isCommercial ? 'business enquiry' : 
                           formData.isCustomQuote ? 'quote request' :
                           formData.isGeneralEnquiry ? 'enquiry' : 'booking';
      setSubmissionError(`An error occurred while submitting your ${submissionType}. ${errorMessage}. Please try again or contact us directly.`);
    } finally {
      setIsLoading(false);
    }
  };

  const handleNewBooking = () => {
    setCurrentPage(1);
    setFormData(initialFormData);
    setSubmissionError(null);
    window.scrollTo(0, 0);
  };

  // Success page component
  const SuccessPage = () => (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-black to-gray-800 flex items-center justify-center">
      <div className="max-w-2xl mx-auto px-6">
        <div className="bg-gradient-to-br from-gray-800 to-gray-900 rounded-2xl p-8 shadow-2xl border border-gray-700 text-center">
          {/* Success Animation */}
          <div className="mb-6">
            <div className="mx-auto w-20 h-20 bg-green-500 rounded-full flex items-center justify-center animate-bounce">
              <svg className="w-10 h-10 text-white" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
              </svg>
            </div>
          </div>

          <h2 className="text-3xl font-bold text-white mb-4">
            {formData.isCommercial || formData.isCustomQuote || formData.isGeneralEnquiry 
              ? 'Enquiry Received!' 
              : 'Booking Confirmed!'}
          </h2>

          {formData.bookingReference && (
            <div className="bg-blue-500/20 border border-blue-500 rounded-lg p-4 mb-6">
              <p className="text-blue-200 text-sm mb-1">Your reference number:</p>
              <p className="text-white font-bold text-xl">{formData.bookingReference}</p>
            </div>
          )}

          <div className="text-gray-300 mb-8 space-y-3">
            <p>Thank you for choosing Somerset Window Cleaning!</p>
            {formData.isCommercial || formData.isCustomQuote || formData.isGeneralEnquiry ? (
              <p>We'll review your enquiry and get back to you within 24 hours with a personalized quote.</p>
            ) : (
              <>
                <p>Your booking has been confirmed for <strong className="text-white">{formatDate(formData.selectedDate)}</strong>.</p>
                <p>We'll be in your area on <strong className="text-white">{formData.visitDay}</strong> and will contact you closer to the date to arrange a specific time.</p>
              </>
            )}
            <p>A confirmation email has been sent to <strong className="text-white">{formData.email}</strong>.</p>
          </div>

          {/* Service Area Information */}
          {formData.serviceArea && (
            <div className="bg-green-500/20 border border-green-500 rounded-lg p-4 mb-6">
              <p className="text-green-200 text-sm">
                <strong>Service Area:</strong> {formData.serviceArea}
              </p>
              {formData.visitDay && (
                <p className="text-green-200 text-sm">
                  <strong>Regular Visit Day:</strong> {formData.visitDay}s
                </p>
              )}
            </div>
          )}

          {/* Pricing Summary for Standard Bookings */}
          {formData.grandTotal > 0 && (
            <div className="bg-gray-700/50 rounded-lg p-4 mb-6">
              <h3 className="text-lg font-semibold text-white mb-2">Service Summary</h3>
              <div className="text-sm text-gray-300 space-y-1">
                <div className="flex justify-between">
                  <span>Service:</span>
                  <span className="text-white">{formData.propertyType} ({formData.bedrooms})</span>
                </div>
                <div className="flex justify-between">
                  <span>Frequency:</span>
                  <span className="text-white">{formData.selectedFrequency}</span>
                </div>
                <div className="flex justify-between border-t border-gray-600 pt-2 mt-2">
                  <span className="font-semibold">Total:</span>
                  <span className="text-green-400 font-bold">£{formatPrice(formData.grandTotal)}</span>
                </div>
              </div>
            </div>
          )}

          <button
            onClick={handleNewBooking}
            className="px-8 py-4 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-lg transition-colors"
          >
            Make Another Booking
          </button>
        </div>
      </div>
    </div>
  );

  return (
    <>
      {/* Progress Bar - Only show on pages 1 and 2 */}
      {currentPage < 3 && (
        <SimpleProgressBar currentStep={currentPage} totalSteps={2} />
      )}

      {/* Error Message */}
      {submissionError && (
        <div className="bg-red-500/20 border border-red-500 rounded-lg p-4 m-6 max-w-4xl mx-auto">
          <p className="text-red-200">{submissionError}</p>
        </div>
      )}

      {/* Page Content */}
      <div className="transition-all duration-300">
        {(() => {
          switch (currentPage) {
            case 1:
              return (
                <div key="page-1" className="animate-fade-in">
                  <ServiceSelectionPage
                    onNext={handleServiceSelectionNext}
                    formData={formData}
                    onFormChange={setFormData}
                  />
                  
                  {/* Social Proof section on first page to build trust */}
                  <div className="container mx-auto px-4 py-8">
                    <SocialProof />
                  </div>
                </div>
              );
            
            case 2:
              return (
                <div key="page-2" className="animate-fade-in">
                  <ContactAndBookingPage
                    onBack={handleContactBack}
                    onSubmit={handleSubmit}
                    formData={formData}
                    onFormChange={setFormData}
                    isLoading={isLoading}
                  />
                  
                  {/* FAQ section on second page to address concerns */}
                  <div className="container mx-auto px-4 py-8">
                    <FAQ />
                  </div>
                </div>
              );
            
            case 3:
              return (
                <div key="page-3" className="animate-fade-in">
                  <SuccessPage />
                </div>
              );
            
            default:
              return (
                <div key="page-1" className="animate-fade-in">
                  <ServiceSelectionPage
                    onNext={handleServiceSelectionNext}
                    formData={formData}
                    onFormChange={setFormData}
                  />
                  
                  {/* Social Proof section on first page to build trust */}
                  <div className="container mx-auto px-4 py-8">
                    <SocialProof />
                  </div>
                </div>
              );
          }
        })()}
      </div>
    </>
  );
}

export default BookingForm;