// Bank holidays and company holidays
export const BLOCKED_DATES = [
  // 2024
  '2024-12-25', // Christmas Day
  '2024-12-26', // Boxing Day
  '2024-12-31', // New Year's Eve
  // 2025
  '2025-01-01', // New Year's Day
  '2025-04-18', // Good Friday
  '2025-04-21', // Easter Monday
  '2025-05-05', // Early May Bank Holiday
  '2025-05-26', // Spring Bank Holiday
  '2025-08-25', // Summer Bank Holiday
  '2025-12-25', // Christmas Day
  '2025-12-26', // Boxing Day
];

// How far in advance customers can book (in days)
export const ADVANCE_BOOKING_DAYS = 60;

// Minimum notice required (in days)
export const MINIMUM_NOTICE_DAYS = 2;

/**
 * Get service area for a postcode
 * @param {string} postcode - The postcode to check
 * @returns {object|null} - Service area object or null if not found
 */
export function getServiceAreaForPostcode(postcode) {
  if (!postcode) return null;
  
  // Extract postcode area (e.g., 'BS1' from 'BS1 2AB')
  const postcodeArea = postcode.replace(/\s+/g, '').substring(0, 3).toUpperCase();
  const postcodeDistrict = postcodeArea.substring(0, 2);
  
  // First try exact match (e.g., BS1)
  for (const [areaKey, area] of Object.entries(SERVICE_AREAS)) {
    if (area.postcodes.includes(postcodeArea)) {
      return { key: areaKey, ...area };
    }
  }
  
  // Then try district match (e.g., BS)
  for (const [areaKey, area] of Object.entries(SERVICE_AREAS)) {
    if (area.postcodes.some(pc => pc.startsWith(postcodeDistrict))) {
      return { key: areaKey, ...area };
    }
  }
  
  return null;
}

/**
 * Get available visit dates for a service area
 * @param {object} serviceArea - The service area object
 * @param {number} maxDays - Maximum days to look ahead
 * @returns {Array} - Array of available dates
 */
export function getAvailableDatesForArea(serviceArea, maxDays = ADVANCE_BOOKING_DAYS) {
  if (!serviceArea) return [];
  
  const availableDates = [];
  const today = new Date();
  const minimumDate = new Date(today.getTime() + MINIMUM_NOTICE_DAYS * 24 * 60 * 60 * 1000);
  
  // Start checking from the minimum date
  let checkDate = new Date(minimumDate);
  checkDate.setHours(0, 0, 0, 0);
  
  for (let i = 0; i < maxDays; i++) {
    const dayOfWeek = checkDate.getDay() === 0 ? 7 : checkDate.getDay(); // Convert Sunday=0 to Sunday=7
    const dateString = checkDate.toISOString().split('T')[0];
    
    // Check if it's the right day of week for this area
    if (dayOfWeek === serviceArea.visitDay) {
      // Check if it's not a blocked date
      if (!BLOCKED_DATES.includes(dateString)) {
        // For bi-weekly frequency, check if it's the right week
        if (serviceArea.frequency === 'weekly' || isCorrectWeekForBiWeekly(checkDate, serviceArea)) {
          availableDates.push({
            date: new Date(checkDate),
            dateString: dateString,
            display: formatDateForDisplay(checkDate),
            areaName: serviceArea.name
          });
        }
      }
    }
    
    checkDate.setDate(checkDate.getDate() + 1);
  }
  
  return availableDates;
}

/**
 * For bi-weekly areas, determine if this is the correct week
 * @param {Date} date - The date to check
 * @param {object} serviceArea - The service area
 * @returns {boolean} - Whether this is a visit week
 */
function isCorrectWeekForBiWeekly(date, serviceArea) {
  if (serviceArea.frequency !== 'bi-weekly') return true;
  
  // Use a reference date to determine odd/even weeks
  // January 1st, 2024 is our reference (week 1)
  const referenceDate = new Date('2024-01-01');
  const weeksDifference = Math.floor((date.getTime() - referenceDate.getTime()) / (7 * 24 * 60 * 60 * 1000));
  
  // Alternate weeks based on area key for distribution
  const areaOffset = Object.keys(SERVICE_AREAS).indexOf(serviceArea.key) % 2;
  return (weeksDifference + areaOffset) % 2 === 0;
}

/**
 * Get the next available visit date for a postcode
 * @param {string} postcode - The customer's postcode
 * @returns {object|null} - Next available date or null
 */
export function getNextAvailableVisit(postcode) {
  const serviceArea = getServiceAreaForPostcode(postcode);
  if (!serviceArea) {
    return null;
  }
  
  const availableDates = getAvailableDatesForArea(serviceArea, 30);
  
  if (availableDates.length === 0) {
    return null;
  }
  
  const nextDate = availableDates[0];
  return {
    ...nextDate,
    serviceArea: serviceArea.name,
    isNextWeek: isNextWeek(nextDate.date),
    dayName: nextDate.date.toLocaleDateString('en-GB', { weekday: 'long' })
  };
}

/**
 * Check if a postcode is in our service area
 * @param {string} postcode - The postcode to check
 * @returns {boolean} - Whether we service this area
 */
export function isPostcodeServiced(postcode) {
  return getServiceAreaForPostcode(postcode) !== null;
}

// Helper functions
function isNextWeek(date) {
  const today = new Date();
  const weekAfter = new Date(today.getTime() + 14 * 24 * 60 * 60 * 1000);
  
  return date >= today && date < weekAfter;
}

function formatDateForDisplay(date) {
  const today = new Date();
  const tomorrow = new Date(today.getTime() + 24 * 60 * 60 * 1000);
  
  if (date.toDateString() === today.toDateString()) return 'Today';
  if (date.toDateString() === tomorrow.toDateString()) return 'Tomorrow';
  
  if (isNextWeek(date)) {
    return date.toLocaleDateString('en-GB', {
      weekday: 'long',
      day: 'numeric',
      month: 'short'
    });
  }
  
  return date.toLocaleDateString('en-GB', {
    weekday: 'short',
    day: 'numeric',
    month: 'short'
  });
}

/**
 * Get all service areas for display
 * @returns {Array} - Array of service areas
 */
export function getAllServiceAreas() {
  return Object.entries(SERVICE_AREAS).map(([key, area]) => ({
    key,
    ...area,
    visitDayName: ['', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'][area.visitDay]
  }));
}